import React , {useState}  from "react"
import CardInfo from '../../templates-info/lesson_card';
import { InView } from 'react-intersection-observer'
import { TimelineLite } from 'gsap/all';
const animate = new TimelineLite();



const CoursesSection =()=> {


  const [interval, setInterval] = useState('')
  const [current ,SetCurrent] = useState(0)
  const image = React.createRef();




    const {heading,description,target,level,img_src,isFree,price,alt,old_price} = CardInfo[current]
    return(
      <InView as="div" onChange={inView => console.log('Inview:', inView)}>
        <ul  className="courses_wrapper" onMouseEnter={() => Enter()} onMouseLeave={() => Leave()} >

<li ref={img => image = img} className="card-lesson">
         <img alt= {alt}  src={img_src}/>
         <div>
           <h3>{heading}</h3>
           <p><small>{description}</small></p>
           <p><strong style={{fontSize:'1rem'}}><u>Цель:</u>{" "}</strong><small>{target}</small></p>
           <p><strong style={{fontSize:'1rem'}}><u>Уровень:</u>{" "} </strong><small>{level}</small></p>
           {!isFree
            ?<div>
                <span>${price}</span>{" "}
                <span style={{fontSize:'1.2rem', color:'grey'}}><del>{old_price && `${old_price}`}</del></span>
             </div>
           :<p>Бесплатно</p>}
           <button><small>Подробнее</small></button>
           </div>
      </li>
      <img alt='arrow-right' src={require('../../images/arrow-right.png')} className = "arrow-right" ></img>
           <img alt='arrow-left' src={require('../../images/arrow-left.png')} className = "arrow-left" ></img>
      </ul>
      </InView>
    )
        
}

export default CoursesSection;